import { React, useState } from 'react'

const Contact = () => {
  


  const sendWhatsapp = (e) => {
    e.preventDefault();
    const mensaje = "Hola soy " + name + " " + lastName + " Mi correo electronico es: " + email + " Soy de la ciudad de: " + city + " Requiero: " + requirements  
    window.location.href = "https://api.whatsapp.com/send?phone=+524433890008&text="+mensaje ;
    
  }
  const [name, setName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [city, setCity] = useState('');
  const [requirements, setRequirements] = useState('');

  return (
    <div className="">

      <header>
        <div className="pricing-header bg-dark text-light mx-3 mb-5 px-5 py-2 pb-md-4 mx-auto text-center">
          <div className="wrapper">
            <h1 className="fw-bold display-5 fs-1">Contáctanos</h1>
          </div>
          <p className="fs-3 ">  Asesoramos en todo proceso de planeación y puesta en marcha de tu evento </p>
        </div>
      </header>

      <form className="row g-3" onSubmit={sendWhatsapp}>
        <div className="col-md-6">
          <label className="form-label">Nombre</label>
          <input 
            type="text" 
            onChange={e => setName(e.target.value)} 
            className="form-control" 
            placeholder="Nombre" 
            aria-label="Nombre" />
        </div>
        <div className="col-md-6">
          <label  className="form-label">Apellido</label>
          <input 
            type="text" 
            onChange={e => setLastName(e.target.value)} 
            className="form-control" 
            placeholder="Apellido" 
            aria-label="Apellido" />
        </div>

        <div className="col-md-6">
          <label className="form-label">Email</label>
          <input 
            type="email" 
            onChange={e => setEmail(e.target.value)} 
            className="form-control" 
            placeholder="Email" />
        </div>
        <div className="col-md-6">
          <label className="form-label">Ciudad</label>
          <input 
            type="text" 
            onChange={e => setCity(e.target.value)} 
            className="form-control" 
            placeholder="Ciudad" />
        </div>

        <div className="form-floating">
          <textarea className="form-control" placeholder="Mandanos tus solicitudes" onChange={e => setRequirements(e.target.value)}  style={{ height: 100 + "px" }}></textarea>
          <label>Escribe tus requisitos...</label>
        </div>

        <div className="col-md-12">
          <button className="btn btn-success" type='submit'>Enviar WhatsApp</button>
        </div>
      </form>
    </div>
  )
}

export default Contact